import Vue from "vue";
import VueRouter from "vue-router";

// 

import login from "@/page/login/index.vue";
// import home from '../page/home.vue'
// import dashboard from "../page/dashboard/index.vue";
import notFound from "@/page/errorPage/404.vue";

import home from "@/page/layout/layout.vue";

import MyMusic from "@/page/mymusic/index.vue";
import MyFrends from "@/page/MyFrends/index.vue";
import playSing from "@/page/mymusic/playForm.vue";
import ShopCity from "@/page/shopCity/index.vue";
import MusicPerson from "@/page/musicPerson/index.vue";
import mvDetail from "@/page/mymusic/mvDetail.vue";
import SearchDash from "@/page/searchDash/index.vue";
import playList from "@/page/playList/index.vue";
import myHome from "@/page/myHome/index.vue";

import loginDash from "@/page/loginDash/index.vue";

import homePlay from "@/page/lunboSong/index.vue";
import happyTime from "@/page/happyTime/index.vue";

//  vue  vueRouter
Vue.use(VueRouter);

// ElementUIvue-router3.0
// const originalPush = VueRouter.prototype.push;
// VueRouter.prototype.push = function push(location) {
//     return originalPush.call(this, location).catch(err => err);
// };

let routes = [{
        path: "/",
        redirect: "dashboard",
        hidden: true
    },
    {
        path: "/login",
        component: login,
        name: "login",
        hidden: true
    },
    {
        path: "/error",
        component: home,
        name: "notFound",
        redirect: "noredirect",
        hidden: true,
        children: [{
            path: "404",
            component: notFound,
            name: "Page404",
            meta: { keepAlive: true } //  }
        }]
    },
    {
        path: "dashboard",
        component: home,
        single: true,
        name: "",
        meta: {
            keepAlive: true // 
        },
        iconCls: "el-icon-s-home", //class
        children: [{
            path: "/dashboard",
            name: "2",
            component: () =>
                import (
                    /*webpackChunkName:'importDashboard',webpackPrefetch: true*/
                    "@/page/dashboard/index.vue"
                ),
            meta: {
                keepAlive: true // 
            }
        }]
    },

    {
        path: "mymusic",
        component: home,
        single: true,
        name: "",
        meta: {
            keepAlive: true // 
        },
        iconCls: "el-icon-s-home", //class
        children: [{
            path: "/mymusic",
            name: "1",
            component: MyMusic,
            meta: {
                keepAlive: true // 
            },
            hidden: true
        }]
    },

    {
        path: "myHappy",
        component: home,
        single: true,
        name: "happy",
        meta: {
            keepAlive: true // 
        },
        iconCls: "el-icon-s-home", //class
        children: [{
            path: "/happyTime",
            name: "happy",
            component: happyTime,
            meta: {
                keepAlive: true // 
            },
            hidden: true
        }]
    },

    {
        path: "playsing",
        component: home,
        single: true,
        name: "",
        meta: {
            keepAlive: true // 
        },
        children: [{
            path: "/playsing",
            name: "singInfo",
            component: playSing,
            meta: {
                keepAlive: true // 
            },
            hidden: true
        }]
    },

    {
        path: "mving",
        component: home,
        name: "mv",
        meta: {
            keepAlive: true
        },
        children: [{
            path: "/collectMv",
            name: "collectMv",
            component: mvDetail,
            meta: {
                keepAlive: true // 
            },
            hidden: true
        }]
    },

    // 

    {
        path: "play-list",
        component: home,
        name: "playlistname",
        meta: {
            keepAlive: true
        },
        children: [{
            path: "/playlist",
            name: "playlist",
            component: playList,
            meta: {
                keepAlive: true
            },
            hidden: true
        }]
    },

    {
        path: "homePlay1",
        component: home,
        name: "homePlay2",
        meta: {
            keepAlive: true
        },
        children: [{
            path: "/homePlay",
            name: "homePlay",
            component: homePlay,
            meta: {
                keepAlive: true
            },
            hidden: true
        }]
    },

    {
        path: "myfrends",
        component: home,
        single: true,
        name: "",
        meta: {
            keepAlive: true // 
        },
        iconCls: "el-icon-s-home", //class
        children: [{
            path: "/myfrends",
            name: "1",
            component: MyFrends,
            meta: {
                keepAlive: true // 
            },
            hidden: true
        }]
    },

    {
        path: "myLoginDash",
        component: home,
        single: true,
        name: "",
        meta: {
            keepAlive: true // 
        },
        iconCls: "el-icon-s-home", //class
        children: [{
            path: "/gotoLogin",
            name: "myLoginDash1",
            component: loginDash,
            meta: {
                keepAlive: true // 
            },
            hidden: true
        }]
    },

    {
        path: "myHome",
        component: home,
        single: true,
        name: "",
        meta: {
            keepAlive: true // 
        },
        iconCls: "el-icon-s-home", //class
        children: [{
            path: "/myHome",
            name: "wode",
            component: myHome,
            meta: {
                keepAlive: true // 
            },
            hidden: true
        }]
    },

    // 

    {
        path: "searchbox",
        component: home,
        single: true,
        name: "search",
        meta: {
            keepAlive: true // 
        },
        iconCls: "el-icon-s-home", //class
        children: [{
            path: "/searchDash",
            name: "searchDash",
            component: SearchDash,
            meta: {
                keepAlive: true // 
            },
            hidden: true
        }]
    },

    {
        path: "shopCity",
        component: home,
        single: true,
        name: "",
        meta: {
            keepAlive: true // 
        },
        iconCls: "el-icon-s-home", //class
        children: [{
            path: "/shopcity",
            name: "1",
            component: ShopCity,
            meta: {
                keepAlive: true // 
            },
            hidden: true
        }]
    },

    {
        path: "musicPerson",
        component: home,
        single: true,
        name: "",
        meta: {
            keepAlive: true // 
        },
        iconCls: "el-icon-s-home", //class
        children: [{
            path: "/musicPerson",
            name: "1",
            component: MusicPerson,
            meta: {
                keepAlive: true // 
            },
            hidden: true
        }]
    },

    {
        path: "/oms",
        // component: home,
        component: home,
        // single: true,
        name: "OMS",
        meta: {
            keepAlive: true // 
        },
        iconCls: "el-icon-shopping-cart-full", //class
        children: [{
                path: "omsOrderUpdate",
                name: "",
                component: () =>
                    import ("../page/OMS/omsOrderUpdate"),
                meta: {
                    keepAlive: true // 
                }
            },
            {
                path: "omsStatusUpdate",
                name: "",
                component: () =>
                    import ("../page/OMS/omsStatusUpdate"),
                meta: {
                    keepAlive: true // 
                }
            },
            {
                path: "omsReturnUpdate",
                name: "",
                component: () =>
                    import ("../page/OMS/omsReturnGoods"),
                meta: {
                    keepAlive: true // 
                }
            },
            {
                path: "omsOrderDetail",
                name: "",
                component: () =>
                    import ("../page/OMS/omsDetailUpdate.vue"),
                meta: {
                    keepAlive: true
                }
            },
            {
                path: "omsReturnDetailUpdate",
                name: "",
                component: () =>
                    import ("../page/OMS/omsReturnDetail.vue"),
                meta: {
                    keepAlive: true
                }
            }
        ]
    },
    {
        path: "/FCS",
        // component: home,
        component: home,
        name: "FCS",
        meta: {
            keepAlive: true // 
        },
        iconCls: "el-icon-message", //class
        children: [{
                path: "compensate",
                component: () =>
                    import ("../page/FCS/FCSCompensate.vue"),
                name: "FCS",
                meta: {
                    keepAlive: true // 
                }
            },
            {
                path: "orderHandle",
                component: () =>
                    import ("../page/FCS/FCSOrderHandle.vue"),
                name: "FCS",
                meta: {
                    keepAlive: true // 
                },
                iconCls: "el-icon-message" //class
            }
        ]
    }

    // { path: "*", redirect: "/error/404", hidden: true }
];
var router = new VueRouter({
    routes
});
export default router;


