// http://3362u865n6.zicp.vip/user/playlist?uid=48693483

import request from "../utils/request";
// ,api
export function getuserplaylist(uid) {
    const userId = {
        uid: uid
    };
    return request({
        url: "api/user/playlist",
        method: "get",
        params: userId
    });
}

// /user/subcount 

export function getUserInfo() {

    return request({
        url: "api/user/subcount",
        method: "get",
    });
}


