import request from "../utils/request";
// ,api
export function nowListenMusic(uid) {
    const id = {
        id: uid
    };
    return request({
        url: "/song/url",
        method: "get",
        params: id
    });
}

// simi/playlist?id=167876
// 
export function getSimPlayList(id) {
    return request({
        url: "api/simi/playlist",
        method: "get",
        params: id
    });
}

// simi/song?id=167876
export function getSimPlaysings(id) {
    return request({
        url: "api/simi/song",
        method: "get",
        params: id
    });
}

// 
export function getalubmInfo(id) {
    return request({
        url: "api/album",
        method: "get",
        params: id
    });
}

// 
// /comment
/**
 * 
 * @param {*} info 
 * 
 * id :  id

tpye:  ,  ,  , mv,  ,  , , 

0: 

1: mv

2: 

3: 

4: 

5: 
6 
 */
export function submitComment(info) {
    var obj = {
        t: 1,
        type: 0,
        id: 0,
        content: ""
    };
    obj = Object.assign(obj, info);
    return request({
        url: "api/comment",
        method: "get",
        params: obj
    });
}
// /comment/album

export function getCommentAlbum(id) {
    var obj = {
        id
    };

    return request({
        url: "api/comment/album",
        method: "get",
        params: obj
    });
}


