import request from "../utils/request";



// 

// /search/suggest

export function getSearchSug(keywords) {
    const k = {
        keywords
    };
    return request({
        url: "api/search/suggest",
        method: "get",
        params: k
    });
}

// mv
// search?keywords= 
// type: 1  10 100  1000  1002  1004mv 1006 1009  1014(vedio), 1018

export function getCloundSearch(keywords) {

    var p = {
        limit: 30,
        type: 1,
        offset: 1 // 
    }

    var p1 = Object.assign(p, keywords)
    return request({
        url: "api/search",
        method: "get",
        params: p1
    });
}


